import React, { useState, useEffect } from 'react';
import { Modal } from 'react-bootstrap';
import { isBrowser, isMobile, browserName, browserVersion, osName, osVersion } from "react-device-detect";

function You(props) {
    const [showAboutYou, setShowAboutYou] = useState();
    const [data, setData] = useState({});
    const [battery, setBattery] = useState({});
    const [ip, setIp] = useState();
    
    const height = window.innerHeight;
    const width = window.innerWidth;
    const angle = window.screen.orientation.angle;
    const mode = window.screen.orientation.type.indexOf('landscape') > -1 ? 'landscape' : 'portrait';
    const referrer = window.document.referrer;
    const memory = window.navigator.deviceMemory;
    const speed = window.navigator.connection.downlink;
    const connection = window.navigator.connection.effectiveType;

    function fetchIP(){
        fetch('https://ipapi.co/json').then(res => res.json()).then(data => setIp(data))
    }
    
    useEffect(() => {
        
        window.navigator.getBattery().then(function(b){
            setBattery({'battery': b.level})
        })
        setData({'height': height, 'width': width, 'angle': angle, 'mode': mode, 'referrer': referrer, 'memory': memory, 'speed': speed, 'connection': connection})
        fetchIP();    
    }, [])
    function showAboutYouHandler(){
        setShowAboutYou(true)
    }
    
    function closeAboutYouHandler(){
        setShowAboutYou(false)
    }
    
    return (
        <div style={{margin: 'auto'}}>
        <button className="about_button" onClick={showAboutYouHandler}>
        <ion-icon name="person-add-outline"></ion-icon>            
        <div style={{display: 'inline', margin: '5px'}}>About You</div>
        </button>
        {showAboutYou && 
        <div className="overlay">
          <Modal.Dialog size="lg" aria-labelledby="contained-modal-title-vcenter" centered>
            <Modal.Header closeButton onClick={closeAboutYouHandler}>
              <Modal.Title>Lets C what I know about YOU</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <p>Hi Explorer, You appear to be visiting my website from ip address {ip.ip} whose internet service provider is {ip.org}</p>
              <p>Your network speed seems to be around <i>{data.speed}mbps</i> which is effectively a <i>{data.connection}</i> connection</p>
              <p>I can see that you are using {isBrowser && <span>desktop</span>}{isMobile && <span>mobile</span>} specifically {browserName} browser of version {browserVersion}</p>
              <p>Your OS seem to be {osName} of version {osVersion} having {data.memory}GB of RAM</p>
              <p>You seem to be at {ip.latitude} latitude and {ip.longitude} longitude in {ip.city} {ip.region} of country {ip.country_name} whose capital is {ip.country_capital} and currency is {ip.currency} {ip.currency_name}</p>
              <p>Your device's screen height is <i>{data.height}</i> pixels and screen width is <i>{data.width}</i> pixels, Your device is currently in the <i>{data.mode}</i> mode and seems to be tilted at a <i>{data.angle}&#176;</i>angle</p>
              <p>Your battery seems to be having <i>{battery.battery * 100}%</i> charge.</p>
              { data.referrer && <p>You seem to have arrived here from the website <i>{data.referrer}</i></p> }
            </Modal.Body>
          </Modal.Dialog>
        </div>  
       }
    </div> 
    );
}

export default You;